import React, { Fragment, useEffect, useState } from 'react'
import { IoMdArrowRoundBack, IoMdBookmark } from 'react-icons/io'
import { MdCheckCircle, MdInfo, MdSearch } from 'react-icons/md'
import { RiErrorWarningLine } from 'react-icons/ri'
import Modal, { ModalSize, Themes } from '../../../components/Modal/Modal'
import { ApplicationStepsTypes } from '../ApplicationSteps/ApplicationSteps'
import * as Interfaces from '../../../interfaces/index';
import { connect } from "react-redux";
import {
  Auth,
  applicationStoreInterface,
  System,
  selectPosition,
  PushPopupAlert,
  Interface_CategorySubCategoryData,
  initiateApplication,
  initialApplicationInterface,
  createApplication,
  FC_GetProfileDetails,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { DocumentsCategories, DocumentSubCategories, PositionRequirements, PositionSubcategoryData, returnValidatePosition, validateDocCategory, validateDocSubcategory, validateDocSubcategoryOptional } from '../../../interfaces/index'
import { AddUserDocument } from '../../../components/AddUserDocument/AddUserDocument'
import { PopupAlertTheme } from '../../../interfaces/popupAlert'
import { PopupAlertData } from '../../../components/Alert/Popup'
import { ApplicationInterface, GetApplicationResponse } from '../../../interfaces/application'
import axios from 'axios'
import { API } from '../../../utils/api'
import { sentExamInterface, SubmitApplicationAPI } from '../ExamCenter/ExamCenter'
import { Redirect } from 'react-router-dom'
import Loading from '../../../components/Loading/Loading'
import { LoadingType } from '../../../components/Loading/LoadingV2'

interface PositionRequirementProps {
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  auth: Auth;
  application: applicationStoreInterface;
  system: System;
  PushPopupAlert: (alert: PopupAlertData) => void;
  createApplication : (user_id: string, application: ApplicationInterface) => void;
  initiateApplication: (initial: initialApplicationInterface) => void;
  FC_GetProfileDetails: Function;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
}

const _PositionRequirement = (props: PositionRequirementProps) => {
  const [requirementsModal, setRequirementsModal] = useState<boolean>(false);
  const [selectedRequirement, setSelectedRequirement] = useState<{ selected_category: DocumentsCategories | null, selected_sub_category: DocumentSubCategories | null, selected_category_subcategory_data?: Interface_CategorySubCategoryData | null, status: boolean } | null>(null);
  const [submitWithoutRequirements, setSubmitWithoutRequirements] = useState<boolean>(false);
  const [redirect, setRedirect] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);
  const [loadUserDetails, setLoadUserDetails] = useState<boolean>(true);

  const sendApplication = async () => {
    // Validation
    if (props.auth.isAuthenticated === true && props.auth.user.user_id !== "") {
      if (props.application.apply) {
        if (props.application.apply.selectedPosition) {
          let exams: sentExamInterface[] = [];
          const applicationDetails: SubmitApplicationAPI = {
            user_id: props.auth.user.user_id,
            position: props.application.apply.selectedPosition.position_id,
            application_period: "2021-2022",
            exams: exams
          }
          try {
            setLoading(true);
            const res = await axios.post<GetApplicationResponse>(`${API.APPLICATION}/application`, applicationDetails);
            console.log("Application res: ", res);
            if (res.status === 200) {
              setRedirect(true);
              props.PushPopupAlert({
                title: `Application created added successfully`,
                theme: PopupAlertTheme.success,
              });
              props.initiateApplication({
                user_id: props.auth.user.user_id, 
                selectedPosition: null, 
                currentStep: ApplicationStepsTypes.Step1,
                positions: null,
                doTestStatus: {
                  status: false,
                },
                selectedLocation: []
              });
            }
            setLoading(false);
          } catch (error: any) {
            setLoading(false);
            if (error.response) {
              if (error.response.data) {
                if (error.response.data.errors !== undefined) {
                  if (error.response.data.errors.length > 0) {
                    props.PushPopupAlert({
                      title: error.response.data.errors[0].message,
                      theme: PopupAlertTheme.warning,
                    });
                    if (error.response.status === 400) {
                      setRedirect(true);
                      props.initiateApplication({
                        user_id: props.auth.user.user_id, 
                        selectedPosition: null, 
                        currentStep: ApplicationStepsTypes.Step1,
                        positions: null,
                        doTestStatus: {
                          status: false,
                        },
                        selectedLocation: []
                      });
                    }
                  }
                } else {
                  props.PushPopupAlert({
                    title: "Something went wrong! Please try again",
                    theme: PopupAlertTheme.danger,
                  });
                }
              }
            }
            console.log("Submit Application err: ", {...error});
          }  
        }
      }
    }
  }

  const validateRequirementItem = (item: PositionRequirements, itm: PositionSubcategoryData) => {
    let x = validateDocSubcategory(item, props.auth.documents).find(foundData => itm._id === foundData.category_subcat_id);
    return x !== null && x !== undefined ? false : true;
  }

  const validateRequirementItemOptional = (item: PositionRequirements, itm: PositionSubcategoryData) => {
    let x = validateDocSubcategoryOptional(item, props.auth.documents).find(foundData => itm._id === foundData.category_subcat_id);
    return x !== null && x !== undefined ? false : true;
  }

  useEffect(() => {
    if (props.auth.documents.length === 0 && loadUserDetails === true) {
      setLoading(true);
      props.FC_GetProfileDetails(
        props.auth.user.user_id,
        (status: boolean, msg: string) => {
          if (status === true) {
            setLoading(false);
            setLoadUserDetails(false);
          } else {
            setLoading(false);
            setLoadUserDetails(false);
          }
        }
      );
      setLoading(false);
      setLoadUserDetails(false);
    }
    if (props.periods.applicationPeriods === null || props.periods.applicationPeriods.length === 0) {
      props.loadApplicationPeriods(setLoading);
    }
  }, [props, loadUserDetails])

  if (redirect === true) {
    return <Redirect to="/applications" />
  }

  return (
    <Fragment>
      <div className="animate__animated animate__zoomIn animate__fast">
        <div className="flex flex-col mb-4 rounded">
          <div className="flex flex-col md:flex-row justify-between mb-2 mt-2 items-start md:items-center w-full">
            <div className="flex flex-col md:flex-row items-center w-full">
              <div className="border-2 border-white h-10 w-10 hidden md:flex justify-center items-center rounded-full bg-blue-50 hover:bg-blue-200">
              <IoMdArrowRoundBack onClick={() => props.setCurrentPosition(null)} className="hidden md:block text-3xl text-blue-600 cursor-pointer" />
              </div>
              <div className="flex flex-col px-0 md:px-2 w-full">
                <span className=" text-xs font-normal text-black-800">Selected position:</span>
                <span className="text-blue-700 text-xl font-bold">{props.currentPosition!.position_title}</span>
              </div>
            </div>
            <div className="mt-2 md:mt-0">
              <button onClick={() => props.setCurrentPosition(null)} className="btn bg-white text-blue-500 border border-blue-200 text-center flex justify-center items-center hover:bg-blue-600 hover:text-white">
                Change
              </button>
            </div>
          </div>
          <div className="hidden md:block border-t-2 border-white">
            <div className="rounded-l flex items-center">
              <div className="text-lg font-bold -mb-2 mt-2 text-gray-800 flex items-center">Exams for the position:</div>
            </div>
            <div className="grid md:grid-cols-12 text-gray-500 gap-2 py-0 px-0 hover:text-blue-600">
              {/* <div className="col-span-1  rounded-l flex items-center">
                <div className="text-lg font-bold mb-0 text-blue-700 flex items-center ml-4">Exams:</div>
              </div> */}
              {props.currentPosition && props.currentPosition.courses.length === 0 ? 
              <div className="col-span-11 py-5 px-3 text-gray-400 flex flex-col justify-center items-center">
                <MdSearch className="text-7xl" />
                <span className="">No courses found!</span>
              </div> :
              props.currentPosition?.courses.map((item, i) => (
                <div key={i + 1} className="col-span-3 flex flex-row items-center py-2 px-2 bg-white rounded mt-2">
                  <div className="h-8 w-8 px-2 py-2 text-lg font-bold bg-gray-50 rounded-full flex items-center justify-center">{i + 1}</div>
                  <div className="flex flex-col ml-2"><span className="font-bold">{item.course_name}</span><span className="text-xs text-gray-600">CODE: {item.course_code}</span></div>
                </div>
              ))}
            </div>
          </div>
        </div>
        {props.auth.documents.length === 0 && loading === true ? 
        <div className="mt-5 bg-white rounded p-4">
          <span className="font-bold text-gray-500 ml-4 mb-4 italic animate-pulse">Checking position requirements, Please wait...</span>
          <div className="my-2"></div>
          <Loading type={LoadingType.white} />
        </div>
        :
        <div className="bg-white px-2 py-2 rounded shadow">
          {/* Mobile view */}
          <div className="block md:hidden border bg-gray-100 mt-2 rounded px-2 py-1">
            <div><span className="font-bold text-lg text-gray-700">Exams for the position</span></div>
            <div className="mt-2 text-xs text-gray-600">
                {props.currentPosition && props.currentPosition.courses.length === 0 ? 
                <div className="py-5 px-3 text-gray-600 font-bold flex flex-col justify-center items-center">
                  <MdSearch className="text-7xl" />
                  <span className="">No courses found!</span>
                </div> :
                props.currentPosition?.courses.map((item, i) => (
                  <div key={i + 1} className="font-bold text-blue-600">({i + 1}) {item.course_code}-{item.course_name}</div>
                ))}
            </div>
          </div>

          <div className="mt-2">
            <span className="font-bold text-xl md:text-xl text-blue-600 pl-3">Requirements for the position</span>
          </div>
          <div className="my-4">

            <div className="flex flex-row justify-between items-center bg-white hover:bg-gray-50 px-2 py-2 my-2 rounded gap-2 cursor-pointer border">
              <div className="flex flex-row items-center gap-2">
                <div><IoMdBookmark className="text-4xl text-gray-500" /></div>
                <div className="flex flex-col px-2">
                  <span className="text-lg font-bold text-gray-500">Psychometric Test</span>
                  <span className="text-xs text-gray-600">Psychometric tests are used to measure an individual's' mental capabilities and behavior.</span>
                </div>
              </div>
              <div>
                <div onClick={() => props.setApplicationStep(ApplicationStepsTypes.Step2)} className="btn border border-blue-500 flex-row items-center text-primary bg-white w-max cursor-pointer hidden">Do test</div>
                <MdCheckCircle className="text-blue-500 text-4xl" />
              </div>
            </div>
            {/* Requirements section */}
            {props.auth.user.id !== undefined && props.currentPosition?.requirements.map((item, i) => (
              <div key={i + 1}className={`flex flex-col px-2 py-2 my-2 rounded gap-2 cursor-pointer ${item !== null && props.auth.documents !== null && validateDocCategory(item, props.auth.documents) !== null && item.position_requirement.length === 0 ? "bg-yellow-50 text-yellow-800 border border-yellow-100" : "bg-gray-100 text-gray-500 hover:bg-gray-100"}`}>
                <div className="flex flex-row items-center justify-between">
                  <div className="flex flex-row items-center gap-2">
                    <div><IoMdBookmark className="text-4xl" /></div>
                    <div className="flex flex-col px-2 w-full">
                      <span className="text-xl font-bold mb-2">{item.category.title}</span>
                      {/* {console.log("User miiss: ", item.position_requirement)} */}
                    </div>
                  </div>
                  <div className="flex items-center justify-end">
                    {item !== null && props.auth.documents !== null && (validateDocCategory(item, props.auth.documents) !== null && validateDocCategory(item, props.auth.documents) !== undefined) && item.position_requirement.length === 0 ?
                    <div onClick={
                      () => setSelectedRequirement(
                        {
                          selected_category: {
                            description: item.category.description,
                            fields: item.category.fields,
                            title: item.category.title,
                            user_detail_category_id: item.category.user_detail_category_id
                          }, 
                          selected_sub_category: null,
                          status: true
                        })} className="btn border border-yellow-500  flex-row items-center text-yellow-800 bg-white w-max cursor-pointer">Add document</div> :
                    <MdCheckCircle className="text-blue-500 text-4xl" />}
                  </div>
                </div>
                {item.position_requirement.length > 0 &&
                <div className="w-full overflow-x-auto">
                  <table className="table-auto bg-white w-full border rounded">
                    <thead className=" text-gray-900 text-lg">
                      <tr className="border border-gray-300">
                        <th className="text-left px-2 py-2">#</th>
                        <th className="px-2 text-left py-2 w-2/3">Document</th>
                        <th className="px-2 text-left py-2">Status</th>
                        <th className="text-right"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {item.position_requirement.map((itm, j) => (
                        <tr key={j + 1} onClick={
                          () => 
                          (validateRequirementItemOptional(item, itm) === false ||
                          validateRequirementItem(item, itm) === false) &&
                          setSelectedRequirement(
                            {
                              selected_category: {
                                description: item.category.description,
                                fields: item.category.fields,
                                title: item.category.title,
                                user_detail_category_id: item.category.user_detail_category_id
                              },
                              selected_sub_category: {
                                category_id: item.subcategory.category_id,
                                description: item.subcategory.description,
                                title: item.subcategory.title,
                                user_detail_subcategory_id: item.subcategory.user_detail_subcategory_id
                              }, 
                              selected_category_subcategory_data: {
                                category_id: item.category.user_detail_category_id,
                                description: itm.description,
                                others: [],
                                subcategory_id: item.subcategory.user_detail_subcategory_id,
                                title: itm.title,
                                _id: itm._id,
                              },
                              status: true 
                            })} className={`border border-gray-300 ${validateRequirementItem(item, itm) === false ? "text-yellow-700" : itm.required === true ? "text-blue-700" : "text-gray-600"} hover:font-bold mb-1`}>
                          <td className="px-2 text-lg font-bold">{j + 1})</td>
                          <td key={j + 1} className="rounded px-2 py-1 w-max cursor-pointer">
                            <span className="font-bold ">{item.subcategory.title} in {itm.title}</span>
                          </td>
                          {validateRequirementItem(item, itm) === false ? 
                            <td className="hidden px-2 text-sm py-2 italic md:flex flex-row items-center gap-2"><RiErrorWarningLine className="text-2xl" /> Not found</td>
                              : 
                              itm.required === true ? 
                            <td className="hidden px-2 py-2 text-sm italic md:flex flex-row items-center gap-2"><MdCheckCircle className="text-2xl" />Found</td>
                              :
                            <td className="hidden px-2 py-2 text-sm italic md:flex flex-row items-center gap-2"><MdInfo className="text-2xl" />Optional</td>
                          }
                          {validateRequirementItem(item, itm) === false ? 
                          <td className="px-2 text-sm text-right py-2">
                            <div className="bg-yellow-100 rounded px-3 py-2 flex items-center justify-center text-sm border border-yellow-400 font-bold text-yellow-800 w-max float-right hover:border-yellow-700">Add document</div>
                          </td> :
                          validateRequirementItemOptional(item, itm) === false ?
                          <td className="px-2 text-sm text-right py-2">
                            <div className="bg-gray-100 rounded px-3 py-2 flex items-center justify-center text-sm font-bold text-gray-800 w-max float-right hover:border-gray-700">Add document</div>
                          </td> :
                          <td className="px-2 py-2 text-sm"></td>
                          }
                          
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>}
              </div>
            ))}
          </div>
          <div>
            <div className="flex flex-row justify-between items-center">
              <button onClick={() => props.setCurrentPosition(null)} className="btn bg-white text-gray-600 border border-gray-300">Back</button>
              {props.application.apply && props.application.apply.user_id !== undefined && props.currentPosition !== null && returnValidatePosition(props.currentPosition.requirements, props.auth.documents) === true &&
              <button onClick={() => props.setApplicationStep(ApplicationStepsTypes.Step3)} className="btn bg-green-500 text-white">Choose examination center</button>}
              {props.application.apply && props.application.apply.user_id !== undefined && props.currentPosition !== null && returnValidatePosition(props.currentPosition.requirements, props.auth.documents) === false &&
              <button onClick={() => setSubmitWithoutRequirements(true)} className="btn bg-blue-100 text-blue-600">Continue anyway</button>}
            </div>
          </div>
        </div>}
      </div>
      {/* Add missing document modal */}
      {selectedRequirement !== null &&
      <AddUserDocument
        active={selectedRequirement.status}
        close={() => setSelectedRequirement(null)}
        category={selectedRequirement.selected_category}
        sub_category={selectedRequirement.selected_sub_category}
        category_subcategory_data={selectedRequirement.selected_category_subcategory_data}
        UploadCompleted={() => {
          setSelectedRequirement(null);
          props.PushPopupAlert({
          title: `Data added successfully`,
          theme: PopupAlertTheme.success,
        })}}
      />}
      {requirementsModal === true &&
        <Modal
          title="Add requirement"
          backDrop={true}
          theme={Themes.primary}
          close={() => setRequirementsModal(false)}
          backDropClose={true}
          widthSizeClass={ModalSize.large}
          displayClose={true}
          padding={{title: true, body: true}}
        >
          <div>
            content
          </div>
        </Modal>
      }
      {submitWithoutRequirements === true &&
        <Modal
          title=""
          backDrop={true}
          theme={Themes.warning}
          close={() => setSubmitWithoutRequirements(false)}
          backDropClose={true}
          widthSizeClass={ModalSize.medium}
          displayClose={false}
          padding={{body: true}}
        >
          {loading === true ? <Loading /> : 
          <div className="">
            <div className="flex flex-row items-center gap-3">
              <RiErrorWarningLine className="text-9xl text-yellow-700" />
              <div className="flex flex-col gap-2">
                <span className="text-xl font-extrabold text-gray-700">Submit application without requirements?</span>
                <span className="text-xs text-gray-800">
                  To submit the application without filling all requirements, make sure that you know your decision because you have low chance to get shortlisted!
                </span>
              </div>
            </div>
            <div className="flex flex-row justify-between items-center">
              <div onClick={() => setSubmitWithoutRequirements(false)} className="btn bg-gray-100 text-gray-800 cursor-pointer">
                Back
              </div>
              <div onClick={() => sendApplication()} className="btn bg-yellow-700 text-white hover:bg-yellow-900 cursor-pointer">
                Yes, I confirm
              </div>
            </div>
          </div>}
        </Modal>
      }
    </Fragment>
  )
}

const mapStateToProps = ({
  auth,
  application,
  system,
  periods,
}: StoreState): { auth: Auth; application: applicationStoreInterface; system: System; periods: applicationPeriodsStoreInterface; } => {
  return { auth: auth,  application: application, system: system, periods: periods, };
};

const PositionRequirement = connect(mapStateToProps, {
  selectPosition,
  PushPopupAlert,
  initiateApplication,
  createApplication,
  FC_GetProfileDetails,
  loadApplicationPeriods,
})(_PositionRequirement);

export default PositionRequirement;