import React from 'react'
import { RiSearchLine } from 'react-icons/ri'

export enum NoResultThemes {
  Dark = "Dark",
  Light = "Light",
}

interface NoResultProps {
  theme: NoResultThemes;
  value: string;
  component?: any;
}

const NoResult = (props: NoResultProps) => {
  return (
    <div className={`flex flex-col py-4 justify-center items-center text-gray-500 border-2 border-white ${props.theme === NoResultThemes.Dark ? "bg-gray-100" : "bg-white"} rounded`}>
      <RiSearchLine className="text-6xl text-gray-500 animate__animated animate__shakeX" />
      <span className="text-xl mb-3">{props.value}</span>
      {props.component !== undefined &&
      props.component}
    </div>
  )
}

export default NoResult
